label{
    color: white;
    margin-bottom: 0.5rem;
}
form{
    display: flex;
    flex-direction: column;
    padding: 4rem 1rem;
    margin: auto;
    max-width: 600px;
}
form input, form textarea{
    margin-bottom: 1rem;
    padding: 10px 18px;
    font-size: 1.2rem;
    background-color: rgb(255,255,255,0.1);   
    border-color: rgb(255,255,255,0.1);
    color: #f4f4f4;
}
form .btn{
    display: flex;
    align-items: center;
    width: 20%;
    justify-content: center;
}
form .buttn{
    display: flex;
    align-items: center;
    justify-content: center;
}
